<template name='infoTime'>
	<view class='layout-time'>
		<view class='time-box'>
			<view class='mmflex-start'>
				<text class='time-text mmfont-14sb' space='nbsp' v-if='Time.Title'>{{Time.Title}}  </text>
				<text class='time-text mmfont-14sb' space='nbsp'>首 {{Time.First}}</text>
				<text class='mmsplit' space='nbsp'> | </text>
				<text class='time-text mmfont-14sb' space='nbsp'>末 {{Time.Last}}</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default({
		name: "infoTime",
		props: {
			data: {
				type: Object,
				default: function() {
					return {};
				}
			}
		},
		mounted() {
			console.log("infoTime父组件data定义的变量", this.data);
			console.log("data", this.Time);
		},
		data() {
			return {
				Time: this.data.Time
			}
		}
	})
</script>

<style>
	.mmflex-start {
		display: flex;
		width: 100%;
		height: 100%;
		align-items: center;
		justify-content: flex-start;
	}
	.mmsplit {
		font-size: 17px;
		font-weight: 600;
		line-height: 20px;
		color: #CCCCCC;
	}
	.layout-time {
		position: relative;
		width: 100%;
		height: 20px;
	}
	.time-box {
		position: absolute;
		left: 92px;
		right: 0px;
		top: 0px;
		bottom: 0px;
	}
	.time-text {
		line-height: 20px;
		color: #83929E;
	}
	.mmfont-14sb {
		font-size: 14px;
		font-weight: 600;
	}
</style>
